class Blob {
    constructor(px, py) {
        this.minx = px
        this.miny = py
        this.maxx = px
        this.maxy = py
    }

    distSq(x1, y1, x2, y2) {
        return (x2-x1)*(x2-x1) + (y2-y1)*(y2-y1)
    }

    render(ctx) {
        ctx.lineWidth = 4;
        ctx.strokeStyle = '#fff'
        ctx.beginPath()
        ctx.rect((68 - this.minx) * 20 - ((this.maxx - this.minx) * 20), this.miny * 20, (this.maxx - this.minx) * 20, (this.maxy - this.miny) * 20)
        ctx.stroke()
    }

    add(px, py) {
        this.minx = Math.min(this.minx, px)
        this.miny = Math.min(this.miny, py)
        this.maxx = Math.max(this.maxx, px)
        this.maxy = Math.max(this.maxy, py)
    }

    isNear(px, py) {
        let c = this.center()
        let d = this.distSq(c.cx, c.cy, px, py)
        if(d < 50) {
            return true
        }
        return false
    }

    size() {
        return Math.abs((this.maxx - this.minx) * (this.maxy - this.miny))
    }

    center() {
        return { cx: (this.minx + this.maxx) / 2, cy: (this.miny + this.maxy) / 2 }
    }
}

class ColourTracker {
    constructor() {
        this.blobs = [ [], [] ]

        this.ox = [ 0, 0]
        this.oy = [ 0, 0]
    }

    setColours(list) {
        this.colours = list
    }

    clearBlobs(index) {
        if(index !== null) {
            this.blobs[index] = []
        } else {
            this.blobs = [ [], [] ]
        }
    }

    updateBlobs(j, px, py) {
        let found = false
        for(let i = 0; i < this.blobs[j].length; i++) {
            if(this.blobs[j][i].isNear(px,py)) {
                this.blobs[j][i].add(px, py)
                found = true
                break;
            }
        }
        if(!found) {
            this.blobs[j].push(new Blob(px,py))
        }
    }

    initHands(g) {
        g.lefthand.x = 0
        g.lefthand.y = 0
        g.righthand.x = 0
        g.righthand.y = 0
    }

    update(giant) {
        if(giant) {

            for(let i = 0; i < this.blobs.length; i++ ) {
                let index = this.findBiggest(i);
                if(index !== null) {
                    let c = this.blobs[i][index].center()
                    
                    let dx = (this.ox[i] - c.cx) * 20
                    let dy = (this.oy[i] - c.cy) * -1 * 20
                    if(dx == 0) dx = null
                    if(dy == 0) dy = null
                    if( i < 1 ) {
                        giant.moveLeft(dx, dy) 
                    } else {
                        giant.moveRight(dx, dy)
                    }
                    this.ox[i] = c.cx
                    this.oy[i] = c.cy
                }
            }
        }
    }

    findBiggest(j) {
        let maxs = 1
        let index = null
        let count = 0
        this.blobs[j].forEach(b => {
            if(b.size() > maxs) {
                maxs = b.size()
                index = count
            }
            count++
        })
        return index;
    }

    render(c) {
        for(let i = 0; i < this.blobs.length; i++ ) {
            let index = this.findBiggest(i);
            if(index !== null) {
                this.blobs[i][index].render(c)
            }
        }
    }
}

export default ColourTracker