import * as util from './util.js'
import { playsound } from './sound.js';

class Button {
    constructor(x, y, width, height, text, bgcolour, hcolour, colour, fontSize, margin) {
        this.x = x
        this.y = y
        this.width = width
        this.height = height
        this.text = text
        this.bgcolour = bgcolour
        this.colour = colour
        this.fontSize = fontSize
        this.margin = margin
        this.hcolour = hcolour
        this.highlight = false
        this.enabled = true
    }

    render(ctx) {
        //draw bg box
        if(this.bgcolour) {
            if(this.highlight) { ctx.fillStyle = this.hcolour } else { ctx.fillStyle = this.bgcolour }
            ctx.fillRect(this.x, this.y, this.width, this.height)
        }
        if(this.fontSize) {
            ctx.font = 'bold ' + this.fontSize + 'px Arial'
        }
        ctx.fillStyle = this.colour
        ctx.textBaseline = "hanging"
        ctx.fillText(this.text, this.x + this.margin, this.y + this.margin, this.width)
    }

    hover(mx, my, c) {
        if(this.enabled && util.contains(mx, my, this.x, this.y, this.width, this.height)) {
            this.highlight = true
            c.style.cursor = 'pointer'
        } else {
            this.highlight = false
            c.style.cursor = 'default'
        }
        return this.highlight
    }

    click(mx, my) {
        if(this.enabled && util.contains(mx, my, this.x, this.y, this.width, this.height)) {
            playsound(0)
            this.action()
        }
    }

    action() {}
}

export default Button